.login__button {
  width: 358px;
  height: 50px;
  border-radius: 2px;
  background: #fff;
  color: #000;
  text-align: center;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin: 176px 0 0;
  transition: 1s;
  cursor: pointer;
  border: none;
}

.login__button:hover {
    opacity: 0.85;
  }
  
  @media screen and (max-width: 767px) {
    .login__button {
        min-width: 260px;
        margin-top: 143px;
        font-size: 16px;
    }
  }